@import "vars.scss";
@import "mixins.scss";
@import "fonts.scss";
@import "settings.scss";

* {
    color: $black;
    box-sizing: border-box;
    font-family: $font-family-Montserrat;
}

.main__container {
    width: 100%;
    min-height: 100vh;
    background: linear-gradient(124.13deg, #F4CB38 10.97%, #F4A938 77.98%);
    position: relative;
    overflow: hidden;
}

.bg-content {
    position: absolute;
    top: -100px;
    right: -320px;
    filter: drop-shadow(8px 10px 5px rgba(0, 0, 0, 0.25));
}

.max-width {
    max-width: 1170px;
}


.social-buttons {
    &__link {
        filter: drop-shadow(8px 10px 5px rgba(0, 0, 0, 0.25));
        transition: 0.3s all;

        &:hover {
            opacity: 0.7;
            filter: drop-shadow(8px 10px 5px rgba(0, 0, 0, 0));
        }
    }

    &__link:not(:first-child) {
        margin-left: 21px;
    }
}

.copyright-block {
    margin: auto;
    margin-top: 16px;

    &__title {
        font-weight: 900;
        font-size: 16px;
        line-height: 20px;
        letter-spacing: 0.065em;
        text-transform: uppercase;
    }
}

/*-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/
/* SCROLBAR -=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-*/

::-webkit-scrollbar-button {
    background-image: url("");
    background-repeat: no-repeat;
    width: 6px;
    height: 0px;
  }
  
  ::-webkit-scrollbar-track {
    background-color: #2d3436;
    -webkit-box-shadow: 0px 0px 3px #000 inset;
    box-shadow: 0px 0px 3px #000 inset;
  }
  
  ::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: #fff;
    -webkit-box-shadow: 0px 1px 1px #b2bec3 inset;
    box-shadow: 0px 1px 1px #b2bec3 inset;
    background-image: url("");
    background-position: center;
    background-repeat: no-repeat;
  }
  
  ::-webkit-resizer {
    background-image: url("");
    background-repeat: no-repeat;
    max-width: 3px;
    height: 0px;
  }
  
  ::-webkit-scrollbar {
    width: 4px;
  }

.gui {
    &__container-logo {
        display: flex;
        align-items: center;
    }

    &__container-logo-name-company {
        width: max-content;
        font-size: 18px;
        line-height: 22px;
        font-weight: 900;
        letter-spacing: 0.065em;
        text-transform: uppercase;
    }

    &__block-logo {
        width: 138px;
        height: 115px;
    }

    &__logo {
        width: 100%;
        height: 100%;
    }

    &__block-phone {
        display: flex;
        align-items: center;
    }
    
    &__block-icon {
        width: 17px;
        height: 17px;
        margin-right: 10px;
    }
    
    &__svg-phone {
        width: 100%;
        height: 100%;
    }
    &__use-phone {}

    &__phone-number {
        font-size: 15px;
        line-height: 18px;
        font-weight: bold;
    }

    &__default-text {
        font-weight: 400;
        font-size: 19px;
        line-height: 27px;
    }

    &__default-button {
        display: inline-flex;
        border: none;
        background: linear-gradient(102.4deg, #F52557 36.84%, #ED1C4E 74.32%);
        box-shadow: 0px 7px 35px rgba(242, 34, 84, 0.42);
        border-radius: 110px;
        min-width: 265px;
        min-height: 67px;
        padding: 23px 20px;
        cursor: pointer;
        transition: 0.5s all;

        &:hover {
            box-shadow: 0px 7px 35px rgba(242, 34, 84, 0);
        }

        &-text {
            font-weight: 700;
            font-size: 17px;
            line-height: 21px;
            color: $white;
            margin: auto;
        }
    }


}