.home-page {
    width: 100%;
    margin-top: 36px;
    position: relative;

    &__container {
        margin: 0 auto;
    }

    &__block-tagline {
        max-width: 523px;
    }

    &__title-tagline {
        font-size: 40px;
        line-height: 49px;
        font-weight: 900;
        text-transform: uppercase;
    }

    &__subtitle {
        margin-top: 15px;
    }

    &__button {
        margin-top: 30px;
    }

    &__container-video {
        margin-top: 350px;
    }

    &__video {
        border-radius: 16px;
        filter: drop-shadow(8px 10px 5px rgba(0, 0, 0, 0.25));
    }
}