.card-friend {
    max-width: 25%;
    margin-top: 60px;
    padding-left: 50px;
    padding-right: 50px;
    box-sizing: border-box;

    &__container {
        background-color: $white;
        overflow: hidden;
        border-radius: 16px;
        filter: drop-shadow(8px 10px 5px rgba(0, 0, 0, 0.25));
        transition: 0.4s all;

        &:hover .card-friend__icon {
            margin-left: 15px;
        }
    }

    &__block-img {
        width: 100%;
        max-height: 181px;
        overflow: hidden;
        position: relative;
        padding-bottom: 100%;
    }

    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }

    &__block-text {
        padding: 13px 21px;
    }

    &__block-name {
        display: flex;
        align-items: center;
    }

    &__name {
        font-size: 19px;
        line-height: 20px;
        font-weight: 700;
        color: $orange;
    }

    &__icon {
        width: 15.5px;
        height: 13.14px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: 10px;
        transition: 0.5s all;
    }

    &__svg-arrow {
        width: 100%;
        height: 100%;
    }

    &__use-arrow {}

    &__description {
        margin-top: 10px;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
    }
}