.footer {
    width: 100%;
    margin-top: 200px;
    position: relative;
    padding-bottom: 68px;

    &__container {
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    &__social-buttons {
        display: flex;
        align-items: center;
        margin: auto;
        width: max-content;
    }
}