.contacts-page {
    width: 100%;
    margin-top: 150px;

    &__container {
        margin: 0 auto;
    }

    &__block-contacts {
        background-color: $white;
        border-radius: 16px;
        max-width: 770px;
        padding: 22px;
        margin: auto;
        filter: drop-shadow(8px 10px 5px rgba(0, 0, 0, 0.25));
    }
    
    &__title {
        font-size: 30px;
        line-height: 37px;
        font-weight: 900;
    }

    &__block-text {
        margin-top: 33px;
    }
}