.header {
    width: 100%;
    transition: 0.7s all;

    &__container {
        padding: 29px 0;
        margin: 0 auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
        transition: 0.7s all;
    }

    &__block-navigation {
        display: flex;
        align-items: center;
    }

    &__nav {
        margin-left: 57px;
    }

    &__ul {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__li_active &__link {
        font-weight: 700 !important;

        &::before {
            opacity: 1;
        }
    }

    &__li {
        
    }

    &__li:not(:first-child) {
        margin-left: 57px;
    }

    &__link {
        font-size: 15px;
        line-height: 18px;
        font-weight: 400;
        position: relative;
        transition: 0.3s all;

        &::before {
            content: "";
            position: absolute;
            width: 100%;
            bottom: -8px;
            left: 0;
            right: 0;
            height: 1px;
            background-color: $black;
            opacity: 0;
            transition: 0.3s all;
        }
        
        &:hover::before {
            opacity: 1;
        }
    }

    &__block-burger {
      width: max-content;
      position: absolute;
      cursor: pointer;
      z-index: 100;
      display: none;
    }

    &__line {
        width: 33px;
        height: 3px;
        background-color: $black;
        margin-bottom: 4px;
        transition: 0.3s all;
    }

    &__line_animation {
      transition: 0.3s all;
    }

    &__line_animation:nth-child(1),
    &__line_animation:nth-child(2) {
      margin-bottom: 0;
      height: 1px;
      position: absolute;
    }

    &__line_animation:nth-child(3) {
      opacity: 0;
      transition: 0s all;
    }

    &__line_animation:nth-child(1) {
      transform: rotate(45deg);
      height: 3px;
    }

    &__line_animation:nth-child(2) {
      transform: rotate(-45deg);
      height: 3px;
    }

    &__link-burger {
        width: 35px;
        height: 40px;
    }

    &__burger {
        background-size: cover;
        background-repeat: no-repeat;
        width: 35px;
        height: 40px;
        transition: 0.3s all;
        &:hover {
            opacity: 0.7;
        }
    }
}